'use client';

import { useState, useEffect } from 'react';
import {
  connectWallet,
  disconnectWallet,
  getConnectedAddress,
  getBalance,
} from '@/lib/waypoint';
import {
  setContractAddress,
  createSamurai,
  getSamurai,
  upgradeStat,
  startBattle,
  executeTurn,
  getBattle,
} from '@/lib/contract';

// Types
interface Samurai {
  name: string;
  level: bigint;
  experience: bigint;
  skillPoints: bigint;
  strength: bigint;
  defense: bigint;
  speed: bigint;
  health: bigint;
  battlesWon: bigint;
  battlesLost: bigint;
  exists: boolean;
}

interface Battle {
  player1: `0x${string}`;
  player2: `0x${string}`;
  bet: bigint;
  player1Health: bigint;
  player2Health: bigint;
  isActive: boolean;
  currentTurn: `0x${string}`;
  turnCount: bigint;
}

export default function Home() {
  const [connectedAddress, setConnectedAddress] = useState<string | null>(null);
  const [balance, setBalance] = useState<string>('0');
  const [samurai, setSamurai] = useState<Samurai | null>(null);
  const [samuraiName, setSamuraiName] = useState('');
  const [opponentAddress, setOpponentAddress] = useState('');
  const [betAmount, setBetAmount] = useState('0.01');
  const [currentBattle, setCurrentBattle] = useState<Battle | null>(null);
  const [battleId] = useState<bigint | null>(null);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');

  // Set contract address (you'll need to update this after deployment)
  useEffect(() => {
    // Replace with your deployed contract address
    setContractAddress(
      '0x0000000000000000000000000000000000000000' as `0x${string}`
    );
  }, []);

  // Check wallet connection on mount
  useEffect(() => {
    checkConnection();
  }, []);

  // Load Samurai data when connected
  useEffect(() => {
    if (connectedAddress) {
      loadSamurai();
      loadBalance();
    }
  }, [connectedAddress]);

  const checkConnection = async () => {
    const address = await getConnectedAddress();
    setConnectedAddress(address);
  };

  const loadBalance = async () => {
    if (!connectedAddress) return;
    try {
      const bal = await getBalance(connectedAddress as `0x${string}`);
      setBalance(bal);
    } catch (error) {
      console.error('Failed to load balance:', error);
    }
  };

  const loadSamurai = async () => {
    if (!connectedAddress) return;
    try {
      const samuraiData = await getSamurai(connectedAddress as `0x${string}`);
      setSamurai(samuraiData as Samurai);
    } catch (error) {
      console.error('Failed to load Samurai:', error);
    }
  };

  const handleConnect = async () => {
    setLoading(true);
    try {
      const address = await connectWallet();
      setConnectedAddress(address);
      setMessage('Wallet connected successfully!');
    } catch (error) {
      setMessage('Failed to connect wallet');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleDisconnect = () => {
    disconnectWallet();
    setConnectedAddress(null);
    setSamurai(null);
    setMessage('Wallet disconnected');
  };

  const handleCreateSamurai = async () => {
    if (!samuraiName.trim()) {
      setMessage('Please enter a Samurai name');
      return;
    }

    setLoading(true);
    try {
      await createSamurai(samuraiName);
      setMessage('Samurai created successfully!');
      await loadSamurai();
      setSamuraiName('');
    } catch (error) {
      setMessage('Failed to create Samurai');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleUpgradeStat = async (stat: number) => {
    setLoading(true);
    try {
      await upgradeStat(stat);
      setMessage('Stat upgraded successfully!');
      await loadSamurai();
    } catch (error) {
      setMessage('Failed to upgrade stat');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleStartBattle = async () => {
    if (!opponentAddress.trim()) {
      setMessage('Please enter opponent address');
      return;
    }

    setLoading(true);
    try {
      await startBattle(opponentAddress as `0x${string}`, betAmount);
      setMessage('Battle started!');
      setOpponentAddress('');
      setBetAmount('0.01');
    } catch (error) {
      setMessage('Failed to start battle');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleExecuteTurn = async () => {
    if (battleId === null) {
      setMessage('No active battle');
      return;
    }

    setLoading(true);
    try {
      await executeTurn(battleId);
      setMessage('Turn executed!');
      // Reload battle data
      const battleData = await getBattle(battleId);
      setCurrentBattle(battleData as Battle);
    } catch (error) {
      setMessage('Failed to execute turn');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  if (!connectedAddress) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-red-900 via-red-800 to-red-900 flex items-center justify-center">
        <div className="bg-white/10 backdrop-blur-md rounded-lg p-8 text-center">
          <h1 className="text-4xl font-bold text-white mb-8">
            ‚öîÔ∏è Ronin Rivals ‚öîÔ∏è
          </h1>
          <p className="text-white/80 mb-8">
            Connect your Ronin Waypoint wallet to begin your Samurai journey
          </p>
          <button
            onClick={handleConnect}
            disabled={loading}
            className="bg-red-600 hover:bg-red-700 text-white font-bold py-3 px-6 rounded-lg transition-colors disabled:opacity-50"
          >
            {loading ? 'Connecting...' : 'Connect Wallet'}
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-red-900 via-red-800 to-red-900 p-4">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <div className="bg-white/10 backdrop-blur-md rounded-lg p-6 mb-6">
          <div className="flex justify-between items-center">
            <h1 className="text-3xl font-bold text-white">
              ‚öîÔ∏è Ronin Rivals ‚öîÔ∏è
            </h1>
            <div className="text-right">
              <p className="text-white/80 text-sm">
                Connected: {connectedAddress?.slice(0, 6)}...
                {connectedAddress?.slice(-4)}
              </p>
              <p className="text-white/80 text-sm">
                Balance: {parseFloat(balance) / 1e18} RON
              </p>
              <button
                onClick={handleDisconnect}
                className="text-red-300 hover:text-red-100 text-sm"
              >
                Disconnect
              </button>
            </div>
          </div>
        </div>

        {/* Message */}
        {message && (
          <div className="bg-white/10 backdrop-blur-md rounded-lg p-4 mb-6">
            <p className="text-white text-center">{message}</p>
          </div>
        )}

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Samurai Section */}
          <div className="bg-white/10 backdrop-blur-md rounded-lg p-6">
            <h2 className="text-2xl font-bold text-white mb-4">
              üó°Ô∏è Your Samurai
            </h2>

            {!samurai?.exists ? (
              <div className="space-y-4">
                <p className="text-white/80">
                  Create your Samurai to begin your journey
                </p>
                <input
                  type="text"
                  value={samuraiName}
                  onChange={(e) => setSamuraiName(e.target.value)}
                  placeholder="Enter Samurai name"
                  className="w-full p-3 rounded-lg bg-white/20 text-white placeholder-white/60 border border-white/20"
                />
                <button
                  onClick={handleCreateSamurai}
                  disabled={loading || !samuraiName.trim()}
                  className="w-full bg-red-600 hover:bg-red-700 text-white font-bold py-3 px-6 rounded-lg transition-colors disabled:opacity-50"
                >
                  {loading ? 'Creating...' : 'Create Samurai'}
                </button>
              </div>
            ) : (
              <div className="space-y-4">
                <div className="bg-white/5 rounded-lg p-4">
                  <h3 className="text-xl font-bold text-white mb-2">
                    {samurai.name}
                  </h3>
                  <div className="grid grid-cols-2 gap-2 text-sm text-white/80">
                    <p>Level: {samurai.level.toString()}</p>
                    <p>Experience: {samurai.experience.toString()}</p>
                    <p>Skill Points: {samurai.skillPoints.toString()}</p>
                    <p>Battles Won: {samurai.battlesWon.toString()}</p>
                    <p>Battles Lost: {samurai.battlesLost.toString()}</p>
                  </div>
                </div>

                <div className="bg-white/5 rounded-lg p-4">
                  <h4 className="text-lg font-bold text-white mb-2">Stats</h4>
                  <div className="space-y-2">
                    <div className="flex justify-between items-center">
                      <span className="text-white/80">
                        Strength: {samurai.strength.toString()}
                      </span>
                      <button
                        onClick={() => handleUpgradeStat(0)}
                        disabled={loading || samurai.skillPoints === 0n}
                        className="bg-red-600 hover:bg-red-700 text-white px-3 py-1 rounded text-sm disabled:opacity-50"
                      >
                        +2
                      </button>
                    </div>
                    <div className="flex justify-between items-center">
                      <span className="text-white/80">
                        Defense: {samurai.defense.toString()}
                      </span>
                      <button
                        onClick={() => handleUpgradeStat(1)}
                        disabled={loading || samurai.skillPoints === 0n}
                        className="bg-red-600 hover:bg-red-700 text-white px-3 py-1 rounded text-sm disabled:opacity-50"
                      >
                        +1
                      </button>
                    </div>
                    <div className="flex justify-between items-center">
                      <span className="text-white/80">
                        Speed: {samurai.speed.toString()}
                      </span>
                      <button
                        onClick={() => handleUpgradeStat(2)}
                        disabled={loading || samurai.skillPoints === 0n}
                        className="bg-red-600 hover:bg-red-700 text-white px-3 py-1 rounded text-sm disabled:opacity-50"
                      >
                        +1
                      </button>
                    </div>
                    <div className="flex justify-between items-center">
                      <span className="text-white/80">
                        Health: {samurai.health.toString()}
                      </span>
                      <button
                        onClick={() => handleUpgradeStat(3)}
                        disabled={loading || samurai.skillPoints === 0n}
                        className="bg-red-600 hover:bg-red-700 text-white px-3 py-1 rounded text-sm disabled:opacity-50"
                      >
                        +10
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* Battle Section */}
          <div className="bg-white/10 backdrop-blur-md rounded-lg p-6">
            <h2 className="text-2xl font-bold text-white mb-4">
              ‚öîÔ∏è Battle Arena
            </h2>

            {samurai?.exists ? (
              <div className="space-y-4">
                <div className="bg-white/5 rounded-lg p-4">
                  <h4 className="text-lg font-bold text-white mb-2">
                    Start New Battle
                  </h4>
                  <input
                    type="text"
                    value={opponentAddress}
                    onChange={(e) => setOpponentAddress(e.target.value)}
                    placeholder="Opponent address (0x...)"
                    className="w-full p-3 rounded-lg bg-white/20 text-white placeholder-white/60 border border-white/20 mb-3"
                  />
                  <input
                    type="number"
                    value={betAmount}
                    onChange={(e) => setBetAmount(e.target.value)}
                    placeholder="Bet amount (RON)"
                    step="0.01"
                    min="0.01"
                    max="1"
                    className="w-full p-3 rounded-lg bg-white/20 text-white placeholder-white/60 border border-white/20 mb-3"
                  />
                  <button
                    onClick={handleStartBattle}
                    disabled={loading || !opponentAddress.trim()}
                    className="w-full bg-red-600 hover:bg-red-700 text-white font-bold py-3 px-6 rounded-lg transition-colors disabled:opacity-50"
                  >
                    {loading ? 'Starting Battle...' : 'Start Battle'}
                  </button>
                </div>

                {currentBattle && (
                  <div className="bg-white/5 rounded-lg p-4">
                    <h4 className="text-lg font-bold text-white mb-2">
                      Active Battle
                    </h4>
                    <div className="space-y-2 text-sm text-white/80">
                      <p>
                        Player 1 Health:{' '}
                        {currentBattle.player1Health.toString()}
                      </p>
                      <p>
                        Player 2 Health:{' '}
                        {currentBattle.player2Health.toString()}
                      </p>
                      <p>
                        Current Turn:{' '}
                        {currentBattle.currentTurn === connectedAddress
                          ? 'Your turn'
                          : "Opponent's turn"}
                      </p>
                      <p>Turn Count: {currentBattle.turnCount.toString()}</p>
                    </div>
                    {currentBattle.currentTurn === connectedAddress && (
                      <button
                        onClick={handleExecuteTurn}
                        disabled={loading}
                        className="w-full mt-3 bg-green-600 hover:bg-green-700 text-white font-bold py-2 px-4 rounded-lg transition-colors disabled:opacity-50"
                      >
                        {loading ? 'Executing...' : 'Execute Turn'}
                      </button>
                    )}
                  </div>
                )}
              </div>
            ) : (
              <p className="text-white/80">
                Create a Samurai first to enter battles
              </p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
