import {
  createPublicClient,
  http,
  parseEther,
  formatEther,
  encodeFunctionData,
} from 'viem';
import { saigon } from 'viem/chains';
import { waypointProvider } from './waypoint';

// Import the generated ABI from Hardhat compilation
import RoninRivalsArtifact from '../../artifacts/contracts/Game.sol/RoninRivals.json';

const CONTRACT_ABI = RoninRivalsArtifact.abi;

// Contract address - will be set after deployment
let CONTRACT_ADDRESS: `0x${string}` | null = null;

// Create public client for read operations
const publicClient = createPublicClient({
  chain: saigon,
  transport: http(),
});

// Set contract address after deployment
export const setContractAddress = (address: `0x${string}`) => {
  CONTRACT_ADDRESS = address;
};

// Contract interaction functions
export const createSamurai = async (name: string) => {
  if (!CONTRACT_ADDRESS) throw new Error('Contract not deployed');

  const data = encodeFunctionData({
    abi: CONTRACT_ABI,
    functionName: 'createSamurai',
    args: [name],
  });

  const hash = await waypointProvider.request({
    method: 'eth_sendTransaction',
    params: [
      {
        to: CONTRACT_ADDRESS,
        data,
      },
    ],
  });

  return hash as string;
};

